import Vue from 'vue'
import Router from 'vue-router'
import demo from '@/components/demo'
import MVVM2_1 from '@/components/MVVM2_1'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'demo',
      component: demo
    },
    {
      path: '/MVVM2_1',
      name: 'MVVM2_1',
      component: MVVM2_1
    },
  ]
})



